* {margin: 0; padding: 0;}
body {
  background-color: #ebfaff;
}
.App {text-align: center;
  background-color: #ffff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
  padding: 30px;
  margin: 50px auto;
  max-width: 340px;
}
h1 {
  letter-spacing: 3px;
  font-weight: 500;
  font-size: 22px;
  background-color: #000;
  color: #fff;
  background: linear-gradient(45deg, #35e1e1, #05a6bc);
  border-radius: 10px;
  text-align: center;
  padding: 20px;
 
}
ul {margin-top: 20px;}
ul li {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px dashed #ddd;
  cursor:pointer;
}
ul li:hover {background-color: #ddd;
  font-weight: bold; /* 추가 */
  border-radius: 10px;
  }